import { PiletApi } from "piral-core";
import * as React from "react";
import { Link } from "react-router-dom";
import LandingPage from "../landing";
import SignIn from "../member/login";

export function setupShell(app: PiletApi) {
  //   LAYOUTS REGISTRATION
  app.registerPageLayout("dashboard", ({ children }) => (
    <>
      <div>Header Landing</div>
      <div>Navigation </div>
      {children}
      <div> footer </div>
    </>
  ));

  app.registerPageLayout("landing", ({ children }) => (
    <>
      <div>Header Landing</div>
      <div>Navigation </div>
      {children}
      <div> footer </div>
    </>
  ));

  app.registerPageLayout("member", ({ children }) => (
    <>
      <Link to='/' className='font-sans hover:font-serif'>
        Redirect to Home page
      </Link>
      <div className='capitalize'>Headers Member</div>
      <div>Navigation </div>
      {children}
      <div> footer </div>
    </>
  ));

  app.registerPage("/login", SignIn, {
    layout: "member",
  });
}
